import React from 'react'

export default function ProjectCard({ projects }) {
    return(
      <div class="container">
      {projects.map((data, index) => (
        <div class="student" key={`project_item_${index}`}>
          <ul>
              <li class="title">
                {data.name}
              </li>
              {data.description=='' ? '' : 
                <li>
                  {data.description}
                </li>
              }
              {data.repository=='' ? '' : 
                <li>
                  <a 
                    href={data.repository} 
                    target="_blank" 
                  >Contribute!</a>
                </li>
              }
              {data.webpage=='' ? '' : 
                <li>
                  <a 
                    href={data.webpage} 
                    target="_blank" 
                  >Start learning!</a>
                </li>
              }
              {data.videos=='' ? '' : 
                <li>
                  <a 
                    href={data.videos} 
                    target="_blank" 
                  >Watch videos</a>
                </li>
              }
          </ul>
        </div>
      ))}
      </div>
    ) 
}
